<template>
  <div>
    <template v-if="modify">
      <div slot="header">
        <div class="imgWrap">
          <i v-if="!current_user.image" class="el-icon-user"></i>
          <img
            v-else
            :src="`http://localhost:3000/images/${current_user.image}`"
            alt="프로필사진"
          />
        </div>
        <div>
          <div>{{ current_user.email }}</div>
          <div>{{ current_user.name }}</div>
        </div>
      </div>
      <div class="item">
        가입일 : {{ createAtTime(current_user.createdAt) }}
      </div>
      <div class="btnBrap">
        <el-button @click="modify = true">수정</el-button>
      </div>
    </template>
    <template v-else>
      <div class="inputWrap">
        <p>이름</p>
        <el-input placeholder="이름을 입력해주세요." v-model="name" clearable>
        </el-input>
      </div>
      <div class="inputWrap">
        <p>아이디</p>
        <el-input
          placeholder="이메일을 입력해주세요."
          v-model="email"
          clearable
        >
        </el-input>
      </div>
      <div class="inputWrap">
        <p>첨부 파일</p>
        <el-upload
          action="#"
          :on-change="uploadFile"
          class="upload-demo"
          name="attachment"
          :auto-upload="false"
        >
          <el-button slot="trigger" size="small" type="primary"
            >select file</el-button
          >
          <div class="el-upload__tip" slot="tip"
            >jpg/png files with a size less than 500kb</div
          >
        </el-upload>
      </div>
      <div class="btnWrap">
        <el-button @click="modify = false">취소</el-button>
        <el-button @click="updateUser">저장</el-button>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      modify: false,
      name: '',
      email: '',
    }
  },
  computed: {
    ...mapState('auth', ['current_user']),
  },
  methods: {
    createAtTime(date) {
      const createDate = new Date(date)
      const year = createDate.getFullYear()
      const month = createDate.getMonth()
      const day = createDate.getDay()
      return `${year}년 ${month}월 ${day}일`
    },
  },
}
</script>

